/* LoginForm.css */

.login-form {
    font-family: 'Patua One', cursive;
    width: 350px;
    height: 250px;
    margin: 0 auto 0 auto;
    border: rgb(143, 143, 143) solid 3px;
    /* margin: 1.5rem; */
    color: #fff;
    background-image: -webkit-linear-gradient(top left,
    rgba(221, 109, 18, 0.856), rgba(0, 0, 0, 0.753));
    background-image: linear-gradient(to bottom right,
    rgba(221, 109, 18, 0.856), rgba(0, 0, 0, 0.753));
    border-radius: .25rem;
    padding-top: 10px;
}
 .login-head {
    margin-left: 15px;
 }

.Google-Login-Btn {
    margin: 5px 0 0 85px;
}

.login-frm-elem {
    margin: 5px 8px 15px 5px;;

}
.card{
    width: 46.4%;
    display: inline-flex;
    flex-direction: row;
    position: relative;
    border: rgb(143, 143, 143) solid 3px;
    margin: 1.5rem;
    background-image: -webkit-linear-gradient(top left,rgba(221, 109, 18, 0.856), rgba(0, 0, 0, 0.753));
    background-image: linear-gradient(to bottom right,rgba(221, 109, 18, 0.856), rgba(0, 0, 0, 0.753));
    box-shadow: 1px 3px 14px 0px black;
    text-align: left;
    height: 15.5rem;
}



.card-body{
    font-family: 'Patua One', cursive;
    border: none;
    color: white;
    
}

a:hover{
    color:rgba(221, 109, 18, 0);
}

.card:hover{
    text-decoration: none;
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    box-shadow: 20px 20px 20px 0px black;
    background: rgba(145, 67, 3, 0.74);
    text-shadow: 3px 3px 20px black;
}

.wholeCard a:hover{
 -webkit-text-emphasis: none;
         text-emphasis: none;
 background: lightgray;
}



.titleBar{
    width: 96%;
    position: relative;
    top: 5px;
    left: 1.9%;
}

.jumbotron{
    font-family: 'Libre Baskerville', serif;
    background-image: url(https://images.pexels.com/photos/1093909/pexels-photo-1093909.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
    background-position: top right;
    border-radius: 60px;
    color: whitesmoke;
    box-shadow: 1px 3px 14px 0px black;
    height: 22em;
    text-shadow: 1px 1px 5px black;
    width: 100%;
}


.toggle-button{
    display: flex;
    flex-direction: column;
    height: 24px;
    justify-content: space-around;
    width:30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
    position: fixed;
    left: 96%;
    background: rgb(206, 167, 116);
    top: 50%;
    border-radius: 20%;

}

.toggle-button:focus{
    outline: none;
}

.toggle_line{
    width: 24px;
    height: 2px;
    background-color: black;
}

.side-drawer {
    height: 100%;
    background:white;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left:0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    background: rgb(206, 167, 116);
    font-family: 'Libre Baskerville', serif;
    
}

.search{
    position: absolute;
    bottom: 80%;
    right: 8px;
    width: 96%;
    font-family: 'Libre Baskerville', serif;
}

.drawerHead{
    text-align: center;
    font-family: 'Libre Baskerville', serif;
    text-decoration: underline;
    padding-top: 5px;
}


.side-drawer ul {
    height: 100%;
    margin-top: -25px;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li{
    margin: 0.5rem 0;
    font-size: 1.5rem;
    cursor: pointer;
}


.side-drawer a:hover,
.side-drawer a:active,
.side-drawer li:hover{
    color: red;
}


.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}



.side-drawer a {
    color: black;
    text-decoration: none;
    font-size: 1.2rem
}

.nav-link a {
    color: black;
    text-decoration: none;
    font-size: 1.2rem;
    
}

.searchBtn{
  width: 6em;  
  top: 50px;
  left: -32px;
  font-family: 'Libre Baskerville', serif;
}
.backdrop{
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    z-index: 100;
    top: 0;
    left: 0;
}
label{
    display: flex;
}
.chatCard{
    width: 500px;
    height: auto;
}
.postBackground{
    background: white;
    width: 100%;
    height: 100%;
    justify-content: space-around;
    max-width: 100%;
    min-width: 100%;
    border-radius: 15px;
    padding: 5px;
}
.opacityTransition{
    -webkit-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
    z-index: 999;
  }
/* PosterQuiz CSS */
.quizcard-header {
    background-color: #544841;
    color: #ffffff;
}
.quizcard {
    width: 96.4%;
    display: inline-flex;
    flex-direction: column;
    position: relative;
    border: rgb(143, 143, 143) solid 3px;
    margin: 1.5rem;
    background-color: #fff;
    background-image: -webkit-linear-gradient(top left,
    rgba(221, 109, 18, 0.856), rgba(0, 0, 0, 0.753));
    background-image: linear-gradient(to bottom right,
    rgba(221, 109, 18, 0.856), rgba(0, 0, 0, 0.753));
    border-radius: .25rem;
}

.quizcard:hover{
    text-decoration: none !important;
    -webkit-transition: null !important;
    transition: null !important;
    box-shadow: null !important;
    background: null !important;
    text-shadow: null !important;
}

.blockquote {
    border-left: 0 solid #eceeef;
}

.list-choices {
    list-style-type: none;
}

.quiz-choice:hover {
    cursor: pointer;
    text-decoration: none;
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    box-shadow: 3px 3px 3px 0px black;
    background: rgba(145, 67, 3, 0.74);
    text-shadow: 3px 3px 20px black;
}

html{
  height: 100%;
  width: 100%;
}


body {
  background-image: url("https://images.pexels.com/photos/64057/pexels-photo-64057.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");
  background-attachment: fixed;
  background-position: center;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  /* display: inline-flex; */
  justify-content: center;
  width: 100%;
}




